import { useFormik } from "formik";
import {
  Box,
  Button,
  Flex,
  FormControl,
  FormLabel,
  Input,
  VStack
} from "@chakra-ui/react";

export default function App() {
  const formik = useFormik({
    initialValues: {
      website_name: "" ,
      website_url: ""
    },
    onSubmit: (values) => {
      alert(JSON.stringify(values));
    }
  });
  return (
    <Flex bg="gray.50" align="center" justify="center" h="100vh">
      <Box boxShadow='md' width={500} bg="white" p={6} rounded="md">
        <form onSubmit={formik.handleSubmit}>
          <VStack spacing={4} align="flex-start">
            <FormControl>
              <FormLabel htmlFor="website">Website's Name:</FormLabel>
              <Input
                id="website_name"
                name="website_name"
                type="text"
                variant="filled"
                onChange={formik.handleChange}
                value={formik.values.website_name} 
              />
            </FormControl>
            <FormControl isRequired>
            <FormLabel htmlFor="website">Website's URL:</FormLabel>
              <Input
                id="website_url"
                name="website_url"
                type="url"
                variant="filled"
                onChange={formik.handleChange}
                value={formik.values.website_url} 
              />
            </FormControl>
            <Button type="submit" colorScheme="purple">
              Add new record
            </Button>
          </VStack>
        </form>
      </Box>
    </Flex>
  );
}